.widgetsm{
    flex:1;
    padding: 20px;
    margin-right: 20px;
    -webkit-box-shadow: 8px 7px 11px -1px rgba(0,0,0,0.75);
-moz-box-shadow: 8px 7px 11px -1px rgba(0,0,0,0.75);
box-shadow: 8px 7px 11px -1px rgba(0,0,0,0.75);
}

.widgetsmTitle{
    font-size: 22px;
    font-weight: 600;
}
.widgetsmImg{
    width: 40px;
    height: 40px;
    object-fit: cover;
    border-radius: 50%;
}
.widgetsmlist{
    margin: 0;
    padding: 0;
    list-style: none;
}
.widgetsmlistItem{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 20px 0px;
}
.widgetsmuser{
    display: flex;
    flex-direction: column;
}
.widgetsmUserName{
    font-weight: 600;
}
.widgetsmUserTitle{
    font-weight: 300;
}
.widgetsmIcon{
    font-size: 16px;
    margin-right: 5px;
}
.widgetsmButton{
    display: flex;
    align-items: center;
    border-radius: 10px;
    border: none;
    padding: 7px 10px;
    background-color: "#eeeef7";
     color: "#555";
     cursor: pointer;
}
