.widgetlg{
    flex: 2;
    
    -webkit-box-shadow: 8px 7px 11px -1px rgba(0,0,0,0.75);
-moz-box-shadow: 8px 7px 11px -1px rgba(0,0,0,0.75);
box-shadow: 8px 7px 11px -1px rgba(0,0,0,0.75);
}
.widgetLgTable{
    border-spacing: 20px;
    width: 100%;
}
.widgetlgTitle{
    font-style: 22px;
    font-weight: 600;
}

.widgetLgTh{
    text-align: left;
}
.widgetLgUser{
    display: flex;
    align-items: center;
    font-weight: 600;

}
.widgetLgImg{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 10px;
}
.widgetLgDate,
.widgetLgAmount{
    font-weight: 300;
    color: gray;
}
.widgetLgButton{
    padding: 5px 7px;
    border: none;
    border-radius: 10px;
    
}
.widgetLgButtonApproved{
    background-color:#e5faf2;
    color:#3bb077;
    border: none;

}


.widgetLgButtonDeclined{
    background-color: #fff0f1;
    color:#d95087;
    border: none;
    
}

.widgetLgButtonPending{
    background-color :#ebf1fe;
    color:#2a7ade;
    border: none;

}