.sidebar{
    flex: 1;
    height: calc(100vh -50px);
    background-color: rgb(251, 251, 255);
    position: sticky;
    top: 50px;
}
.sideWrapper{
    padding: 20px;
    color: #555;

}
.sidebarMenu{
    margin-bottom: 10px;
    
}
.sidebartitle{
    font-size: 13px;
    color: lightgray;
}
.sidebarList{
    list-style: none;
    padding: 5px;
}
.sidebarListitem{
    padding: 5px;
    cursor: pointer;
    display: flex;
    align-items: center;
    border-radius: 10px;
    
}
.sidebarListitem.active,
.sidebarListitem:hover{
background-color: rgb(202, 202, 238);
}
.sidebarIcon{
        margin-right: 5px;
    font-size: 20px !important;
}    


